// Styles
.App {
    background: #eee;
}
// National Flags
.nation-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 2rem;
  padding: 0.1rem 0.5rem;
  width: 100%;
}

.nation-flag {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0.2rem;
}

.flag {
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em,
    rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
    rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}

/* Emotion */
.emotion-div {
  padding: 1rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.emotions {
  width: 16rem;
  display: flex;
}
.emotion {
  margin: 0.25rem;
  width: 1.5rem;
  height: 1.5rem;
  border: 0;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.5rem;
  font-weight: 600;
  outline: 0;
  position: relative;
  text-align: center;
  text-decoration: none;
  -webkit-text-stroke: 1px black;
  transition: all 0.3s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  box-shadow: 0px 5px 10px #bbb, 0px -5px 10px white;
}

.emotion:before {
  background-color: initial;
  background-image: linear-gradient(#fff 0, rgba(255, 255, 255, 0) 100%);
  border-radius: 125px;
  content: "";
  height: 50%;
  left: 4%;
  opacity: 0.5;
  position: absolute;
  top: 0;
  transition: all 0.3s;
  width: 92%;
}

.emotion:hover {
  box-shadow: rgba(255, 255, 255, 0.2) 0 3px 15px inset,
    rgba(0, 0, 0, 0.1) 0 3px 5px, rgba(0, 0, 0, 0.1) 0 10px 13px;
}

